'use client';
import { useState, useEffect } from 'react';

export default function Songs() {
  const [data, setData] = useState(null);  // Initialize as null to handle no data state
  const [isLoading, setLoading] = useState(true);

  useEffect(() => {
    fetch('https://api.genius.com/songs/378195', {
      method: 'GET',
      headers: {
        'Authorization': 'Bearer GQA8Epkxvfor7m1qOZk57HzZFNfRyWb6eEldv7OpRTvz9nPUcnO0xixykjjyQVU6ZplQNSObrP6If5j5yW3mXQ',
      },
    })
      .then((res) => res.json())
      .then((data) => {
        setData(data);  // Update the state with the fetched data
        setLoading(false);  // Set loading to false once the data is fetched
      })
  }, []);

  if (isLoading) return <p>Loading...</p>;
  if (!data) return <p>No data found</p>;

  return (
    <div>
      <h1>{data}</h1>
    </div>
  );
}